:root
{
  --yellow: #405deb;
    --orange : #6a6db4;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(47, 71, 177, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App{
    padding: 0.5rem 4.5rem;
    overflow: hidden;
    color: var(--black);  
    align-items: center;  
    align-content: center;
}

.button{
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 16px;
  padding: 10px 26px 11px 26px ;
  background: linear-gradient(180deg, #574cbb 26.71%,#4f4faa 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(155, 149, 212, 0.42);
}

.button:hover{
  background: white;
  cursor: pointer;
  color: var(--orange);
  border: 1px solid var(--orange);
}
  
@media  screen and(max-width: 600px) {
  .App{
    padding: 0.5rem 1rem;
  }
}

  